.analogikello {
  background-color: gray;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

th, td {
  text-align: left;
  padding: 5px;
}

th {
  background-color: orange;

}
tr:nth-child(even) {
  background-color: #f2f2f2
}




#deletetbl th, td {
  text-align: left;
  padding: 5px;
}

#deletetbl th {
  background-color: orange;

}
#deletetbl tr:nth-child(even) {
  background-color: #f2f2f2
}
#deletetbl tr:nth-child(odd) {
  background-color: #f2f2f2
}
.center{
    font-family: 'Raleway', sans-serif;
    background-color: #1abc9c;
    position: absolute;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card{
    background-color: #fff;
    border-radius: 15px;
    padding: 0.8rem;
}

.card > form{
    display: flex;
    flex-direction: column;
}

.card h1{
    text-align: center;
    margin-top: 0;
    margin-bottom: 10px;
}

.form-item{
    font-family: 'Raleway', sans-serif;
    padding: 5px;
    margin-bottom: 2rem;
    height: 30px;
    width: 16rem;
    border: 1px solid grey;
}

.form-submit{
    font-family: 'Raleway', sans-serif;
    height: 35px;
    color: #fff;
    background-color: #1abc9c;
    border: none;
    letter-spacing: 0.2rem;
    -webkit-transition: 0.3s opacity ease;
    transition: 0.3s opacity ease;
    cursor: pointer;
}
.form-submit:hover{
   opacity: 0.6;
}
